<template>
  <div>
    <!-- create a button for every available (depends on data_input) request in veux store query_object-->
    <div v-for="(query_object, index) in availableRequests" :key="index" style="padding: 3px">
        <div v-if="(this.selectedTargetsIdsLength == 1 && query_object.value_type == 'integer')
                     || (this.selectedTargetsIdsLength >= 1 && query_object.value_type == 'integer_array')">
            <!-- firsts sets the input to the current ids, then creates a request that will output in the veux query_object.response -->
            <button @click="this.$store.commit('setQueryData', {'obj':query_object,
                                                                'name': 'value', 
                                                                'data': this.$store.state.selected_item_ids}), 
                            this.$store.dispatch('submitObj', query_object)">{{query_object.title}}
                            
            </button>
        </div>
        <!-- if the request is disabled (because you maybe selected too many), disable the button -->
        <div v-else>
            <button disabled>{{query_object.title}}</button>
        </div>
    </div>
  </div>
</template>

<script>
export default {
    name: 'Requesters',
    data () {
        return {

        }
    },
    methods: {
        
    },
    created() {  
    },
    computed: {
        selectedTargetsIdsLength (){
            return this.$store.getters.selectedItems.length
        },
        selectedType(){

        },
        availableRequests(){
            return this.$store.state.query_objects.filter((e) => {return e.input == this.$store.state.input_type})
        },

    },
}
</script>

