<template>
<div>
    <div >
      <!-- input search field -->
      <input v-on:keydown.enter="this.$store.commit('clickItem', filteredList[0])" type="text" placeholder="search filters..." v-model="search" autofocus="true">
    </div>
    <div>
      <!-- toggle buttons -->
      <div :v-model="toggleAll">
        <button @click="toggleAll(filteredList)">Toggle All</button>
      </div>
      <div :v-model="selectAll">
        <label for="selectAll">Select All</label>
        <input type="checkbox" id="selectAll" @click="selectAll(filteredList)" v-model="allSelected">
      </div>
      <div :v-model="clearAll">
        <label for="selectAll">Clear All</label>
        <input type="checkbox" id="selectAll" @click="clearAll(filteredList)" v-model="noneSelected">
      </div>
      <!-- dropdown menu to select the type of input you want to request -->
      <select v-model="inputType" @change="clearAll(this.$store.getters.selectedItems)">
         <option value="target">Targets</option>
         <option value="question">Questions</option>
      </select>
      <!-- number selected -->
    <div>{{this.$store.getters.selectedItems.length}} SELECTED</div>
    </div>

    <div  class="scrollable list">
      <ul id="customScroll">
        <!-- create an input list item for every target or quetion (depending on filtered list (depending on listOfItemType)) -->
        <!-- clickItem is a veux muation that adds or removes (if already selected) an id from selected_item_ids -->
        <div v-for="(filter, index) in filteredList" :key="index" @click="this.$store.commit('clickItem', filter); updateInputBox()"  >
        <!-- link that input to selectItems, selectItems is the model that changes the veux store selected_items_ids, and checks the checkbox if in that list -->
          <input :value="filter" v-model="selectedItems" type="checkbox">
          <label >{{filter.label}}</label >
          <label style="float: right;">{{filter.id}}</label>

        </div>
      </ul>
    </div>
    <input type="text" v-model="input_box" v-on:input="updateSelectedItemsFromInputBox" />
    <p>Selected: </p>
    <!-- shows the selected items, and removes (because they're already selected) them on click -->
    <div v-for="(filter, index) in this.$store.getters.selectedItems" :key="index" @click="this.$store.commit('clickItem', filter); updateInputBox()" class="small-box" :data-tooltip=filter.description >
      <input :value="filter" v-model="selectedItems" type="checkbox">
      <label>{{filter.label}}</label>      
    </div>

  </div>

</template>

<script>
export default {
  name: 'SearchableList',
  props: {
  },
  data() {
    return {
      search: "",
      input_box: "",
      dropdown: true,
    };
  },
  computed: {
    // make multisearch available
    searchList() {
      return this.search.toLowerCase().split(',');
    },
    // returns a list of targets or questions (depends on listOfItemType) that have one of the search for items in them
    filteredList() {
      return this.listOfItemType.filter((item) => {
        if (this.searchList[0] == "" || this.searchList[-1] == " ") {
          return true
        } else
        if (this.searchList.some((searchItem) => item.label.toLowerCase().includes(searchItem) && searchItem !== '')) {
          return true
        } else 
        if (this.searchList.some((searchItem) => item.id == searchItem)) {
          return true
        } else return false
      }
      );
    },
    // returns list of targest or question (depends on selected input_type)
    listOfItemType(){
      if (this.inputType == 'target'){
        return this.$store.state.target_data
      } else 
      if (this.inputType == 'question'){
        return this.$store.state.question_data
      }
    },
    // return true if all filtered items are selected, has to have a set() so you can click the checkbox without an error
    allSelected: {
      get() {return this.filteredList.every((filtered_item) => this.$store.getters.selectedItems.includes(filtered_item)) },
      set(val){}
    },
    // return true if all filtered items are deslected
    noneSelected: {
      get() {return this.filteredList.every((filtered_item) => !this.$store.getters.selectedItems.includes(filtered_item)) },
      set(val){}
    },
    // selectedItemInputField: {
    //   get() {return this.$store.state.selected_item_ids},
    //   set (value) {this.$store.commit('setSelectedItemIds', value.split(',').map((e) => {return Number(e)}))}
    //   },
    selectedItems: {
      set(value){this.$store.commit('updateSelectedItems', value); this.updateInputBox()},
      get(){ return this.$store.getters.selectedItems}
    },
    // sets the veux store input type from the select tool
    inputType: {
      set (value){this.$store.commit('setInputType', value)},
      get (){return this.$store.state.input_type}
    }
  },
  methods: {
    // set the input box to the selected ids in veux store
    updateInputBox(){
      this.input_box = this.$store.state.selected_item_ids
    },
    updateSelectedItemsFromInputBox: function(){
      this.$store.commit('setSelectedItemIds', this.input_box.split(',').map((e) => {return Number(e)}))
    },
    // clear=true in selectAll removes all items from the selected id list (if they are there) 
    // otherwise it selects them
    toggleAll: function(items) {
      const all_selected = this.allSelected
      this.$store.commit('selectAll', {'items':items,'clear': all_selected})
      this.updateInputBox()
    },
    selectAll: function(items) {
      this.$store.commit('selectAll', {'items':items,'clear': false})
      this.updateInputBox()
    },
    clearAll: function(items) {
      this.$store.commit('selectAll', {'items':items,'clear': true})
      this.updateInputBox()
    },
  },
}
</script>

<style scoped>
.small-box{
  float: left; 
  margin-left: 10px; 
  box-sizing: content-box; 
  border: 2px solid steelblue;
  padding-left: 3px;
  padding-right: 3px;
background: lightsalmon;
}
.scrollable{
  overflow-y: scroll;
}
.list{
  border: 1px solid steelblue;
  padding: 5px;
  margin: 2px;
  height: 200px;
}
</style>