<template>
<div>
    <!-- if a query_object is sending, display loading, if nothing is sending and nothing recieved: you did not send request, if not sending and has response: display query_object that has a response title -->
        <p v-if="(this.$store.state.query_objects[this.indexOfLastQueryItem].sending == true)">Loading ...</p>
        <p v-else-if="(lastQueryObj.response.length == 0)">Did not send request</p>
        <p v-else>{{this.$store.state.query_objects[indexOfLastQueryItem].title}}:</p>
    <div>
        <div v-if="(indexOfLastQueryItem !== -1)">
            <ul class="scrollable list">
                <!-- for every returned id, create a list item that handles descriptions -->
                <div v-for="(listItem , index) in listItems" :key="index">
                    <OutputListItem :label="listItem.label" :id="listItem.id" :description="listItem.description" />
                    <hr noshade="true" size="2px">
                </div>
            </ul>
        </div>
    </div>
</div>
</template>

<script>
import OutputListItem from '../components/OutputListItem'

export default {
    name: 'ResponseOutput',
    components: {
    OutputListItem,
    OutputListItem
    },
    methods: {
    },
    computed: {
        indexOfLastQueryItem (){
            return  this.$store.state.query_objects.map((e) => {return e.path}).indexOf(this.$store.state.last_clicked_query_object_path)
        },
        // last clicked query_object
        lastQueryObj (){
            if (this.indexOfLastQueryItem == -1) {
                return {response: [], path: "", sending: false}
            } else {
                return this.$store.state.query_objects[this.indexOfLastQueryItem]
            }
        },
        // check if the last clicked object is waiting for respone
        recievedDataStatus (){
            if (this.lastQueryObj.sending == true) {
                return 'sending'
            } else 
            if (this.lastQueryObj.response == []) {
                return 'noData'
            } else {
                return 'data'
            }
        },
        // these are the list for the data that a request returns, this is used later for formatting
        // made this like this, so you can add new request easier
        targetListResponsePaths () {
            return this.$store.state.query_objects.filter((e) => {return e.returns == 'target_list'}).map((e) => {return e.path})
        },
        questionListResponsePaths () {
            return this.$store.state.query_objects.filter((e) => {return e.returns == 'question_list'}).map((e) => {return e.path})
        },
        targetResponsePath () {
            return this.$store.state.query_objects.filter((e) => {return e.returns == 'target'}).map((e) => {return e.path})
        },
        questionResponsePath () {
            return this.$store.state.query_objects.filter((e) => {return e.returns == 'question'}).map((e) => {return e.path})
        },
        // list the response items (this list is being displayed in the list), this formats the returned data according to the returned data type
        listItems (){
            if (this.targetListResponsePaths.includes(this.lastQueryObj.path)) {
                var returned_target_data = this.$store.state.target_data.filter((e) => {return this.lastQueryObj.response.includes(e.id)})
                return returned_target_data.map((e) => {return {'label': e.label, 'id': e.id, 'description': e.description}})
            } else 
            if (this.questionListResponsePaths.includes(this.lastQueryObj.path)) {
                var returned_question_data = this.lastQueryObj.response
                return returned_question_data.map((e) => {return {'label': 'some question', 'id': e, 'description': 'some question description'}})
            } else
            if (this.targetResponsePath.includes(this.lastQueryObj.path)) {
                var returned_target_data = this.$store.state.target_data.filter((e) => {return this.lastQueryObj.response.includes(e.id.parseInt())})
                return [{'label': returned_target_data.label, 'id': returned_target_data.id, 'description': returned_target_data.description}]
            } else 
            if (this.questionResponsePath.includes(this.lastQueryObj.path)) {
                var returned_question_data = this.$store.getters.formatQuestion(this.lastQueryObj.response)
                return [{'label': 'some question', 'id': returned_question_data.id, 'description': returned_question_data.description}]
            }
        },
    }
}
</script>

<style>
.scrollable{
  overflow-y: scroll;
}
.list{
  border: 1px solid steelblue;
  padding: 5px;
  margin: 2px;
  height: 200px;
}
.box {
    border: solid;
    outline-color: gray;
    border-width: 1px;
    padding: 3px;
    margin-top: 3px;
    margin-bottom: 3px;
    font-size: smaller;
}
</style>