<template>
<li>
    <details>
        <summary>
                <label>{{label}}</label>
                <label style="float: right;">{{id}}</label>
        </summary>
        <div class="box">
            <p>Beschrijving:</p>
            <!-- if description is an string, show the string, if it's an obj sort and format the obj and show it nicer -->
            <p class="box" v-if="(typeof description != 'object')">{{description}}</p>
            <div v-else>
                <div  v-for="(key, index) in Object.keys(Object.keys(description)
                                                               .sort()
                                                               .reduce(function (result, key) {
                                                                result[key] = description[key];
                                                                return result;
                                                                }, {}))"
                                                                :key="index">
                    <p>{{key.charAt(key.length - 1)}}: {{description[key]}}</p>            
                </div>
            </div>
        </div>
    </details>
</li>

</template>

<script>
export default {
    name: 'OutputListItem',
    data(){
        return {
            sorted_description: {}
        }
    },
    props: {
        label: String,
        id: [Number, String],
        description: [Object, String],
    },
    computed: {

    },
    methods: {
    },
}
</script>

<style>

.box {
    border: solid;
    outline-color: gray;
    border-width: 1px;
    padding: 3px;
    margin-top: 3px;
    margin-bottom: 3px;
    font-size: smaller;
}
</style>